import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs } from '../components/Content';
import Menu from '../components/Menu'
import MediaQuery from 'react-responsive';
import {Helmet} from "react-helmet";
import Icon from '../assets/img/webicon128.png';


export default class Home extends React.Component{
	componentDidMount() {
		window.scrollTo(0, 0)
	}

	render(){
		return (
			<div className='container'>
				<Helmet>
					<meta charSet="utf-8" />
					<title>บริการไฟแนนซ์รถมือสอง | รถแลกเงิน | รีไฟแนนซ์รถ - Car ATM Thailand</title>
					<link rel="description" content="บริการสินเชื่อมากว่า 30 ปี - ทั้งไฟแนนซ์รถบ้าน สินเชื่อรถแลกเงิน จำนำทะเบียนรถ รีไฟแนนซ์รถยังผ่อนไม่หมด เราให้เงื่อนไขที่ดีกว่า บริการรวดเร็ว พนักงานรับสายมีประสบการณ์ ปิดดีลได้ทันที" />
					<meta rel="canonical" href="https://caratmthailand.com" />
					<meta name="robots" content="index, follow" />
					<meta property="og:type" content="article" />
					<meta property="og:title" content="บริการไฟแนนซ์รถมือสอง | รถแลกเงิน | รีไฟแนนซ์รถ - Car ATM Thailand" />
					<meta property="og:description" content="บริการสินเชื่อมากว่า 30 ปี - ทั้งไฟแนนซ์รถบ้าน สินเชื่อรถแลกเงิน จำนำทะเบียนรถ รีไฟแนนซ์รถยังผ่อนไม่หมด เราให้เงื่อนไขที่ดีกว่า บริการรวดเร็ว พนักงานรับสายมีประสบการณ์ ปิดดีลได้ทันที" />
					<meta property="og:image" content="LINK TO THE IMAGE FILE" />
					<meta property="og:url" content="PERMALINK" />
					<meta property="og:site_name" content="caratmthailand.com" />
					<link rel="shortcut icon" href={Icon}/>
				</Helmet>
				<Header
					style={{marginBottom: 20}}
					slot={
						<div className='largest__text'>
							<MediaQuery maxWidth={768}>
								<div>
									<span className='dark__blue'>จัดไฟแนนซ์</span>
									<span className='blue'>รถมือสอง</span>
								</div>
								<div>
									<span className='dark__blue'>ครบวงจร</span>
								</div>
							</MediaQuery>
							<MediaQuery minWidth={769}>
								<div>
									<span className='dark__blue'>จัดไฟแนนซ์</span>
									<span className='blue'>รถมือสอง</span>
									<span className='dark__blue'>ครบวงจร</span>
								</div>
							</MediaQuery>
						</div>
					}
				/>
				<Menu/>
				<WhyChooseUs/>
				<ContactUs/>
				<AboutUs/>
			</div>
		);
	}
}
