import React from 'react';
import { Route, Switch } from "react-router-dom";
import Navbar from './components/Navbar';
import Home from './pages/Home';
import About from './pages/About';
import Finance from './pages/Finance';
import CarExchange from './pages/CarExchange';
import Refinance from './pages/Refinance';
import NotFound from './pages/NotFound';
import './assets/scss/App.scss'


class App extends React.Component {
	render(){
		return (
			<div>
				<Switch>
					<Route exact path="/" component={Navbar} />
					<Route exact path="/about" component={Navbar} />
					<Route exact path="/usedcarfinance" component={Navbar} />
					<Route exact path="/carforcash" component={Navbar} />
					<Route exact path="/carrefinance" component={Navbar} />
				</Switch>

				<Switch>		
					<Route exact path="/" component={Home} />
					<Route path="/about" component={About} />
					<Route path="/usedcarfinance" component={Finance} />
					<Route path="/carforcash" component={CarExchange} />
					<Route path="/carrefinance" component={Refinance} />
					<Route component={NotFound}/>	
				</Switch>
				
			</div>
		);
	}
}

export default App;
